

























import { Vue, Component, Prop } from 'vue-property-decorator'
import post from '@/util/util'
import store from '@/store'

@Component
export default class FreeboardCard extends Vue {
  @Prop({ default: [] }) boardList: any

  board: any = {}
  contentData: any = {
    currentBoard: {}
  }

  clickCard (boardInfo: any) {
    this.$router.push('/freeboardView/' + boardInfo.boadNmbr)
  }

  goPreboardList () {
    this.$router.push('FreeboardList/free')
  }
}
